import { memo } from "react"
import useDateDmy from "../../Hooks/useDateDmy"
import useJenisKelamin from "../../Hooks/useJenisKelamin"

const EditSkck: React.FC<{
    dataSurat: DataT
    formControlInputSurat: string
}> = ({ dataSurat, formControlInputSurat }) => {
    const { ayah, ibu, surat } = dataSurat
    const { pemohon } = surat
    const { alamat } = pemohon

    const dateDmyBuilder = useDateDmy()
    const jkParsing = useJenisKelamin()
    return (
        <>
            <section id="isi-surat">
                <table>
                    <tbody>
                        <tr>
                            <td colSpan={3}>
                                <p className="paragraf">
                                    Yang bertanda tangan di bawah ini, Kepala
                                    Kelurahan Jembatan Kecil Kecamatan Singaran
                                    Pati Kota Bengkulu, dengan ini menerangkan
                                    bahwa:
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="label">
                                <p>Nama</p>
                                <p>Tempat Tanggal Lahir</p>
                                <p>Jenis Kelamin</p>
                                <p>Bangsa/Agama</p>
                                <p>Pendidikan Terakhir</p>
                                <p>Status</p>
                                <p>Pekerjaan</p>
                                <p className="alamat">Alamat</p>
                                <p>Kecamatan</p>
                                <p>Kab/Kotamadya</p>
                                <p>Provinsi</p>
                                <p>Nomor NIK/KTP</p>
                                <p>Nama Bapak</p>
                                <p>Nama Ibu</p>
                                <p>Bangsa Agama</p>
                                <p className="alamat">Alamat</p>
                                <p>Keperluan/Maksud</p>
                            </td>
                            <td className="pembatas">
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p className="alamat">:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p className="alamat">:</p>
                                <p>:</p>
                            </td>
                            <td className="data-surat">
                                <p>{pemohon.nama}</p>
                                <p>{`${
                                    pemohon.tempat_lahir || "Bengkulu"
                                }, ${dateDmyBuilder(
                                    pemohon.tanggal_lahir || "2000-01-01"
                                )}`}</p>
                                <p>{jkParsing(pemohon.jenis_kelamin)}</p>
                                <p>{`Indonesia / ${pemohon.agama}`}</p>
                                <p>{pemohon.pendidikan}</p>
                                <p>{pemohon.status}</p>
                                <p>{pemohon.jenis_pekerjaan}</p>
                                <p className="alamat">{alamat.alamat}</p>
                                <p>{alamat.kecamatan}</p>
                                <p>{alamat.kabupaten}</p>
                                <p>{alamat.provinsi}</p>
                                <p>{pemohon.nik}</p>
                                <p>{ayah.nama_lengkap}</p>
                                <p>{ibu.nama_lengkap}</p>
                                <p>{`Indonesia / ${ayah.agama}`}</p>
                                <p className="alamat">{ayah.alamat}</p>
                                <p>
                                    Persyaratan untuk{" "}
                                    <span
                                        suppressContentEditableWarning
                                        contentEditable
                                        className={formControlInputSurat}
                                        data-name="keperluan"
                                    >
                                        {dataSurat?.keperluan}
                                    </span>
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="ket-surat" colSpan={3}>
                                <p
                                    className={formControlInputSurat}
                                    data-name="keterangan"
                                    contentEditable
                                    suppressContentEditableWarning
                                >
                                    {dataSurat?.keterangan}
                                </p>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </section>
        </>
    )
}

export default memo(EditSkck)
